}

.alert .close {
    position: relative;
    top: 0px;
    right: -22px;
    color: #FFFFFF;
    text-shadow: none;
    line-height: 20px;

    -webkit-transition: opacity 0.1s linear 0s;
    -moz-transition: opacity 0.1s linear 0s;
    -ms-transition: opacity 0.1s linear 0s;
    -o-transition: opacity 0.1s linear 0s;
    transition: opacity 0.1s linear 0s;
